:root {
  --p: 259 94% 51%;
  --pf: 259 94% 41%;
  --pc: 0 0% 100%;
  --s: 314 100% 47%;
  --sf: 314 100% 37%;
  --sc: 0 0% 100%;
  --a: 159 41% 54%;
  --af: 159 41% 54%;
  --ac: 0 0% 100%;
  --n: 215 28% 17%;
  --nf: 221 39% 11%;
  --nc: 0 0% 100%;
  --b1: 0 0% 100%;
  --b2: 210 20% 98%;
  --b3: 216 12% 84%;
  --bc: 215 28% 17%;
  --in: 207 90% 54%;
  --su: 101 56% 42%;
  --wa: 36 100% 50%;
  --er: 14 100% 57%;
