html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #1e1e1f;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
    Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 14px;
  color: rgb(220, 220, 220);
  margin: 0;
  padding: 0;
}

main {
  min-height: 700px;

  @media (max-width: 1023px) {
    min-height: auto;
  }
}

.accordion-item .accordion-title {
  padding-right: 35px;
}

img.invert {
  -webkit-filter: invert(100%);
  filter: invert(100%);
}

img.invertl {
  -webkit-filter: invert(25%);
  filter: invert(25%);
}

.features {
  & img {
    min-height: 180px;
    max-height: calc(100vw / 1.78);
    // cursor: zoom-in;
  }
}

.mockup-window {
  padding-top: 0.5rem;
  max-height: calc(100vw / 1.78);

  &::before {
    margin-bottom: 0.5rem;
  }
}

.tools {
  width: 100%;
  max-width: 510px;
}

.tools__item {
  width: 11.11%;
}

.textarea {
  height: 28rem;
  background: #3c4850;
  &::placeholder {
    font-size: 1rem;
    line-height: 1.4rem;
    @media (max-width: 1024px) {
      font-size: 1rem;
    }
  }

  @media (max-width: 645px) {
    // padding-top: 5rem;
  }

  @media (max-width: 345px) {
    // padding-top: 8rem;
  }
}

