a.questions {
  color: #2388ba;
  text-decoration: underline;
}

.btn--custom {
  font-size: 1rem;
}

.telegram-iframe-container {
  @media (max-width: 767px) {
    position: relative;
    margin-top: 1rem;
  }
}

.telegram-iframe {
  @media (max-width: 767px) {
    width: 100%;
    height: 100%;
  }
}

.loading {
  position: relative;
  &::before {
    position: absolute;
    right: 0;
    margin-left: 0.5rem;
  }
}

input {
  background: #3c4850 !important;
}

input:focus {
  box-shadow: 0px 0px 0px 2px #607482 !important;
}

textarea:focus {
  box-shadow: 0px 0px 0px 2px #607482 !important;
}

.iframe-block {
  min-width: 100px;
  height: 640px;
  margin-top: -35px;

  @media (max-width: 1023px) {
    margin-top: 0;
    height: 650px;
  }
}

.translate {
  color: #59b998;
  text-decoration: underline;
}

.translate--active {
  color: #ffffff !important;
  text-decoration: none;
  cursor: none;
  pointer-events: none;
}

.translate {
  color: #59b998;
}

.tools__item--disco {
  z-index: 10;
}

.disco-element {
  z-index: 1;
  max-height: 15rem;
  width: auto;
  object-fit: contain;

  // animation-duration: 1s;
  // animation-name: dance;
  // animation-iteration-count: infinite;
  // animation-direction: alternate;
}

.disco-element--banana {
  // max-height: 12rem;
}

@keyframes dance {
  from {
    height: 6rem;
  }

  to {
    height: 12rem;
  }
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 4px solid RGB(148, 202, 102);
  border-right: 4px solid RGB(255, 87, 68);
  border-bottom: 4px solid #D861BE;
  border-left: 4px solid RGB(255, 206, 87);

  width: 20px;
  height: 20px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}